}

html,
body,
#root,
.App {
  height: 100%;
}

.swiper-container {
  width: 375px;
  height: 100%;
}

.page1 img,
.page2 img,
.page3 img,
.page4 img,
.page5 img,
.page6 img,
.page7 img,
.page8 img{
  width: 100%;
  /* position: absolute; */
