}

.page1,
.page2,
.page3,
.page4,
.page5,
.page6,
.page7,
.page8{
  width: 375px;
  /* v--viewport ,h-height,1vh --1%视图的高度，100vh-->100%视图的高度 */
  height: 100vh;
  background: url(../../static/media/bg.8df7caa0.jpg);
  background-size: 100%;
  position: relative;
  overflow: hidden;
  /* opacity: 0; */
}









